<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddApkPage" size="mini" icon="el-icon-plus">添加Apk版本</el-button>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="100" prop="versionName" label="版本名称"></el-table-column>
        <el-table-column min-width="150" prop="des" label="更新说明"></el-table-column>
        <el-table-column min-width="100" prop="apkType" label="Apk类型" :formatter="apkTypeFormater"></el-table-column>
        <el-table-column min-width="210" prop="url" label="下载地址"></el-table-column>
        <el-table-column min-width="100" prop="versionCode" label="内部版本号"></el-table-column>
        <el-table-column min-width="100" prop="updateType" :formatter="formatUpdateType" label="升级类型"></el-table-column>
        <el-table-column min-width="160" prop="updateTime" label="发布时间"></el-table-column>
        <el-table-column min-width="70" label="操作">
          <template slot-scope="scope">
            <div>
              <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
            </div>
            <div>
              <el-button @click="deleteItem(scope.row.id)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
 
const apk = {
  // 获取apk列表
  getApkList: '/plateform/a/apk/version/getApkList',
  // 删除apk版本信息
  deleteApk: '/plateform/a/apk/version/deleteApk'
}
export default {
  data() {
    return {
      loading: false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      searchContent: {
        startDate: '',
        endDate: '',
        user: '',
        moduleType: 0,
        key: ''
      },
      tableData: [] // 列表
    }
  },
  mounted() {
    let searchContentMesg = Storage.get("searchContentForAPK")
    if(this.$route.meta.keepAlive && searchContentMesg) {
      this.pageNum = searchContentMesg.pageNum,
      this.searchContent = searchContentMesg.searchContent

      Storage.remove('searchContentForAPK')
    }
    // 查询
    this.doSearchHandel()
  },
  components: {},
  methods: {
    /**
     * [formatUpdateType 格式化升级类型]
     * @return {[type]} [description]
     */
    formatUpdateType (row, column, cellValue) {
      if(cellValue == 0) {
        return '正常'
      }else if(cellValue == 1) {
        return '强制升级'
      }
    },
    /**
     * [changeSize 分页]
     * @param  {[type]} _val [description]
     * @return {[type]}      [description]
     */
    changeSize(value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 修改选中page]
     * @param  {[type]} value [description]
     * @return {[type]}       [description]
     */
    changeCurrent(value) {
      this.pageNum = value
      this.doSearchHandel()
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel(_parme) {
      if(_parme === true) {
        this.pageNum = 1
      }
      this.loading = true
      this.$http.post(apk.getApkList, {pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.result && res.data.result.length) {
            this.tableData = res.data.result
            this.total = res.data.total
          }else {
            this.tableData = []
            this.total = 0
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [goAddApkPage 添加Apk]
     * @return {[type]} [description]
     */
    goAddApkPage() {
      this.$router.push('/apk/add')
    },
    /**
     * [editItem 编辑]
     * @return {[type]} [description]
     */
    editItem(_row) {
      // 设置搜索条件
      let _paramMesg = {
        pageNum: this.pageNum,
        searchContent: this.searchContent
      }
      Storage.set("searchContentForAPK", _paramMesg)
      Storage.set("ApkDate", _row)
      this.$router.push('/apk/edit')
    },
    /**
     * [deleteItem 删除Apk]
     * @return {[type]} [description]
     */
    deleteItem(_row) {
      this.$confirm('您确定要删除所选Apk版本吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.post(apk.deleteApk, {id: _row}).then(res => {
          if(res.code === 0) {
            this.$message.success("删除成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }).catch(() => {})
    },
    apkTypeFormater(row, column, cellValue) {
      if (cellValue === 0) {
        return "门店端"
      } else if (cellValue === 1) {
        return "TV端"
      } else {
        return cellValue
      }
    }
  },
  /**
   * [beforeRouteLeave 页面离开]
   * @param  {[type]}   to   [description]
   * @param  {[type]}   from [description]
   * @param  {Function} next [description]
   * @return {[type]}        [description]
   */
  beforeRouteLeave (to, from, next){
    from.meta.keepAlive = false;
    next();
  }

}
</script>


